@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@1,600&family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&family=Qahiri&family=Tourney:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&family=Qahiri&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Titillium Web',"Roboto";

}
.navbar-brand{
  color:rgba(184, 45, 20, 0.74);
}
.navbar-brand{
    color:rgb(80, 80, 235)!important;
    font-family: 'Style Script';
    font-size: 35px;
    font-weight: bolder !important;
    
  
    padding-top: 30px;
    margin-top: 15px;
    margin-bottom: 6px;
}
.nav-link{
    padding-top: -20px;
    color:rgb(80, 80, 235)!important;
    font-style: bold;
    font-family:"Lato";
    font-weight: bolder;
    padding-bottom: 30px;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    justify-self: flex-end;
    
 
  
}
.nav-link:hover{
    color:rgb(80, 80, 235)!important;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    text-decoration: underline;
}
.navbar-nav{
    margin-top: -30px;
}





.btn {
    will-change: transform;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
    background-color:white !important;
    border-radius: 50px;
}
  
.btn:hover {
    background-color:rgb(80, 80, 235)!important;
    transition: -webkit-transform 125ms;
    transition: transform 125ms;
    transition: transform 125ms, -webkit-transform 125ms;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
#header .brand-name{
    color:rgb(80, 80, 235) ;
}

.btn{
    margin-top:20px;
    color: black;
    margin-bottom: 120px;
}
.btn:hover{
    color: black;
}
